.interests-list {
    display: flex;
    justify-content: space-between;
}

.interests-list_item {
    display: flex;
    flex-flow: column wrap;
    align-items: center;
}

.interests-img {
    width: 6rem;
    height: 6rem;
    margin-bottom: 1rem;
}

.interests-website {
    font-family: 'Playfair Display', serif;
    font-style: italic;
    text-align: center;
    font-size: 2.4rem;
    margin-top: 4.6rem;
}