/* LAYOUT */

.container {
  margin: 6.2rem auto;
  max-width: 880px;
  width: 84%;
  display: flex;
  overflow: hidden;
  padding-bottom: 2rem;
}

.col-left {
  width: 35%;
}

.col-right {
  margin-left: 5rem;
  width: calc(100% - 35%);
}

/* FONTS */

.title {
  font-size: 3.2rem;
  font-family: 'Playfair Display', serif;
  font-weight: bold;
  font-style: italic;
  margin: 4.4rem 0 2.8rem;
  position: relative;
  display: inline-block;

  &::after {
    content: "";
    position: absolute;
    width: 1.2rem;
    height: 1.2rem;
    top: 46%;
    right: -4.2rem;
    background-image: url("golden-diamond.svg");
  }

  &__first {
    margin-top: 3rem;
  }

  &__big {
    font-size: 5.4rem;
    margin: 0;
    &::after {
      background-image: none;
    }
  }
}

.title-skills {
  color: #7666ab;
  font-size: 1.6rem;
  font-weight: bold;
}

.subtitle {
  font-family: 'Playfair Display', serif;
  color: #7666ab;
  font-weight: normal;
  font-size: 3.6rem;
  margin-top: 1rem;
}

.col-right-title {
  font-size: 2.4rem;
  font-weight: 500;
}

.col-right-subtitle {
  font-weight: 400;
  margin-bottom: 2.4rem;
  font-family: 'Playfair Display', serif;
}

.link {
  color: #1d1d1d;
  text-decoration: none;
  border-bottom: 3px solid #7666ab;
  transition: all .5s ease;
  outline: none;

  &:hover, &:focus {
    color: #7666ab;
    border-bottom: 3px solid #1d1d1d;
  }
}

/* COLOR */

.purple {
  color: #7666ab;
}

/* SPACES */

.col-right-spacelist + .col-right-spacelist {
  margin-top: 3.2rem;
}

@media screen and (max-width: 900px) {
  .container {
    flex-flow: column-reverse wrap;
  }

  .col-left, .col-right {
    width: 84%;
    margin: 0 auto;
  }

  .col-right {
    margin-bottom: 9.2rem;
  }
}

@media screen and (max-width: 600px) {
  .col-left, .col-right {
    width: 100%;
  }
}